@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap');

.App {
  font-family: 'Inter', sans-serif;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 75px;
  background: linear-gradient(90deg, rgba(103,34,128,1) 17%, rgba(185,24,218,1) 62%);
  font-weight: 700;
}

.header--img {
  width: 55px;
  padding: 15px;
}

.header--title {
  color: white;
  font-size: 1.75em;
}

.main {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 30px;
}

.main-form {
  width: 70%;
}

.main-inputs {
  display: flex;
  justify-content: center;
  width: 100%;
  gap: 20px;
}

.input {
  height: 40px;
  width: 50%;
  flex-direction: row;
  border: 1px solid #D5D4D8;
  border-radius: 10px;
  text-indent: 10px;
}

.input::placeholder {
  font-size: 16px;
}

.button {
  background: linear-gradient(90deg, rgba(103,34,128,1) 17%, rgba(185,24,218,1) 62%);
  width: 100%;
  padding: 14px;
  margin: 25px 0px;
  border-radius: 10px;
  color: white;
  font-weight: 700;
  font-size: 1em;
  border: none;
}

.meme {
  width: 100%;
  position: relative;
  margin-bottom: 20px;
}

.meme-image {
  width: 100%;
  border-radius: 10px;
}

.meme-text {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow: 
      2px 2px 8px #000,
      2px 2px 0 #000,
      -2px -2px 0 #000,
      2px -2px 0 #000,
      -2px 2px 0 #000,
      0 2px 0 #000,
      2px 0 0 #000,
      0 -2px 0 #000,
      -2px 0 0 #000,
      2px 2px 5px #000;;
}

.top {
  top: 0;
}

.bottom {
  bottom: 0;
}

@media only screen and (max-width: 600px) {
  .main-form {
    width: 80%;
  }

  .main-inputs {
    flex-direction: column;
    gap: 15px;
  }

  .input {
    width: 100%;
  }
}